html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
}

main {
  display: block;
}

h1 {
  font-size: 2em;
  margin: 0 0 0.67em;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: 1em;
}

a {
  background-color: transparent;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

img {
  border-style: none;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

:root {
  --color-white: #fff;
  --color-black: #000;
  --color-primary: #252b42;
  --color-secondary: #374754;
  --color-blue: #2091f9;
  --color-grey: #f2f2f2;
  --color-dark-grey: #d8d8d8;
  --shadow: 0 12px 18px 0 rgba(1,1,1, 0.08);
}

html {
  font-family: "Poppins", sans-serif;
  font-size: 10px;
  height: 100%;
  scroll-behavior: smooth;
}

body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  font-size: 1.6rem;
  min-height: 100%;
  background-color: var(--color-white);
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

a {
  color: inherit;
  text-decoration: none;
}

svg {
  fill: currentColor;
}

[stroke-width] {
  fill: none;
  stroke: currentColor;
}

.overflow-hidden {
  overflow: hidden;
}

.header {
  color: var(--color-white);
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  transition: 0.2s;
}
.header--sticky {
  background-color: var(--color-primary);
}

.header__container {
  max-width: 1440px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 16px;
  padding-right: 16px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  gap: 20px;
  padding-top: 15px;
  padding-bottom: 15px;
}
@media (min-width: 768px) {
  .header__container {
    padding-left: 24px;
    padding-right: 24px;
  }
}
@media (min-width: 1440px) {
  .header__container {
    padding-left: 32px;
    padding-right: 32px;
  }
}
@media (min-width: 768px) {
  .header__container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
}

.header__logo {
  margin-top: -10px;
}

@media (max-width: 991px) {
  .header__nav {
    order: 3;
    margin-left: auto;
  }
}

@media (max-width: 767px) {
  div.header__social {
    display: none;
  }
}
@media (min-width: 992px) {
  div.header__social {
    order: 2;
    margin-left: auto;
  }
}

@media (max-width: 991px) {
  .nav--active {
    width: 40px;
    height: 30px;
  }
  .nav--active .nav__toggle {
    position: fixed;
  }
  .nav--active .nav__toggle-item {
    height: 0;
  }
  .nav--active .nav__toggle-item::before {
    transform: translate(0) rotate(45deg);
  }
  .nav--active .nav__toggle-item::after {
    width: 28px;
    transform: translate(0) rotate(-45deg);
  }
  .nav--active .nav__list {
    transform: none;
  }
}

.nav__toggle {
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--color-white);
  width: 40px;
  height: 30px;
  padding: 0;
  background: none;
  border: none;
  cursor: pointer;
  position: relative;
  z-index: 99;
}
@media (min-width: 992px) {
  .nav__toggle {
    display: none;
  }
}

.nav__toggle-item {
  width: 22px;
  height: 3px;
  background-color: currentColor;
  position: relative;
}
.nav__toggle-item::before, .nav__toggle-item::after {
  content: "";
  width: inherit;
  height: 3px;
  background-color: inherit;
  position: absolute;
  top: 0;
  right: 0;
  transition: transform 0.3s, width 0.2s;
}
.nav__toggle-item::before {
  width: 28px;
  transform: translateY(-8px);
}
.nav__toggle-item::after {
  width: 14px;
  transform: translateY(8px);
}

.nav__list {
  display: flex;
  align-items: center;
  list-style: none;
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
}
@media (max-width: 991px) {
  .nav__list {
    flex-direction: column;
    font-size: 2rem;
    text-align: center;
    width: 100%;
    height: 100%;
    padding: 0 30px;
    background-color: var(--color-primary);
    background-color: rgba(0, 0, 0, 0.85);
    box-sizing: border-box;
    overflow: auto;
    position: fixed;
    top: 0;
    right: 0;
    transform: translateX(100%);
    transition: transform 0.2s;
    z-index: 98;
  }
  .nav__list .nav__item:first-child {
    margin-top: auto;
  }
  .nav__list .nav__item:last-child {
    margin-bottom: auto;
  }
}
@media (min-width: 992px) {
  .nav__list {
    display: flex;
    align-items: center;
  }
}

.nav__item {
  margin-right: 20px;
}
.nav__item:last-child {
  margin-right: 0;
}
@media (max-width: 991px) {
  .nav__item {
    text-align: center;
    margin-right: 0;
  }
}

.nav__link {
  display: block;
  letter-spacing: 0.2px;
  padding: 10px;
  position: relative;
  transition: 0.2s;
}
.nav__link::before {
  content: "";
  width: 0;
  height: 2px;
  background-color: var(--color-blue);
  position: absolute;
  top: 100%;
  left: 50%;
  transition: 0.2s;
}
.nav__link:hover::before, .nav__link:focus-visible::before {
  width: 100%;
  left: 0;
}

.social {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}

.social__link {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  margin-right: 24px;
  transition: 0.2s;
}
.social__link:last-child {
  margin-right: 0;
}
.social__link:hover, .social__link:focus-visible {
  color: var(--color-blue);
}

.swiper {
  width: 100%;
  height: 100%;
  padding: 0 30px;
  box-sizing: border-box;
  position: relative;
}
@media (min-width: 992px) {
  .swiper {
    padding: 0 50px;
    overflow: hidden;
  }
}

.swiper-wrapper {
  display: flex;
  align-items: center;
  width: 100%;
}

.swiper-pagination {
  margin: 10px auto 0;
  position: static;
  transform: none !important;
}

.swiper-pagination-bullet {
  width: 10px;
  height: 10px;
  background-color: var(--color-blue);
}

.swiper-button-prev,
.swiper-button-next {
  color: var(--color-blue);
  width: 30px;
  height: 100%;
  margin-top: 0;
  background-color: var(--color-white);
  border: 2px solid transparent;
  border-radius: 6px;
  box-sizing: border-box;
  top: 0;
  transition: 0.2s;
}
.swiper-button-prev::after,
.swiper-button-next::after {
  font-size: 3.6rem;
}
.swiper-button-prev:hover, .swiper-button-prev:focus-visible,
.swiper-button-next:hover,
.swiper-button-next:focus-visible {
  border-color: var(--color-blue);
}
@media (min-width: 992px) {
  .swiper-button-prev,
  .swiper-button-next {
    width: 50px;
  }
  .swiper-button-prev::after,
  .swiper-button-next::after {
    font-size: 4.4rem;
  }
}

.swiper-button-prev.swiper-button-disabled,
.swiper-button-next.swiper-button-disabled {
  opacity: 1;
}
.swiper-button-prev.swiper-button-disabled::after,
.swiper-button-next.swiper-button-disabled::after {
  opacity: 0.35;
}

.swiper-button-prev {
  left: 0;
}

.swiper-button-next {
  right: 0;
}

.form {
  display: flex;
  flex-direction: column;
  gap: 12px;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.form__input {
  color: #18171D;
  font-size: 1.4rem;
  letter-spacing: 0.1px;
  width: 100%;
  min-height: 54px;
  padding: 10px 35px;
  background-color: #f4f4f4;
  border: 1px solid #e8e8e8;
  border-radius: 38px;
  outline: 2px solid transparent;
  outline-offset: -2px;
  box-sizing: border-box;
  transition: 0.3s;
}
.form__input:last-child {
  margin-bottom: 0;
}
.form__input:hover, .form__input:focus {
  outline-color: var(--color-blue);
}

.form__textarea {
  width: 100%;
  padding: 20px;
  min-height: 190px;
  background-color: #f4f4f4;
  border: 1px solid #e8e8e8;
  box-sizing: border-box;
  outline: 2px solid transparent;
  resize: none;
  transition: 0.3s;
}
.form__textarea:hover, .form__textarea:focus {
  outline-color: var(--color-blue);
}

.form__submit {
  min-height: 54px;
}

.button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  color: var(--color-white);
  font-size: 2rem;
  line-height: 1;
  padding: 16px 60px;
  background-color: var(--color-blue);
  border: 2px solid transparent;
  border-radius: 35px;
  box-sizing: border-box;
  cursor: pointer;
  transition: 0.2s;
}
.button:hover, .button:focus-visible {
  color: var(--color-blue);
  background-color: transparent;
  border-color: var(--color-blue);
}
@media (max-width: 767px) {
  .button {
    padding-left: 30px;
    padding-right: 30px;
  }
}

.intro {
  display: flex;
  color: var(--color-white);
  min-height: 100vh;
  padding-top: 90px;
  padding-bottom: 90px;
  background-color: var(--color-primary);
  background-image: url("../img/bg.jpg");
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;
  box-sizing: border-box;
}

.intro__container {
  max-width: 1440px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 16px;
  padding-right: 16px;
  max-width: 500px;
  margin: auto;
}
@media (min-width: 768px) {
  .intro__container {
    padding-left: 24px;
    padding-right: 24px;
  }
}
@media (min-width: 1440px) {
  .intro__container {
    padding-left: 32px;
    padding-right: 32px;
  }
}
@media (min-width: 992px) {
  .intro__container {
    margin-left: 250px;
  }
}

.intro__title {
  font-size: 5.2rem;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 20px;
}

.intro__desc {
  font-size: 2.2rem;
  line-height: 1.6;
  margin-left: auto;
  margin-right: auto;
}
.intro__desc:first-of-type {
  margin-top: 0;
}
.intro__desc:last-of-type {
  margin-bottom: 0;
}

.intro__button {
  margin: 60px auto 0;
}
.intro__button:hover, .intro__button:focus-visible {
  color: var(--color-white);
}

.main {
  overflow: hidden;
}

.main__container {
  max-width: 1440px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 16px;
  padding-right: 16px;
}
@media (min-width: 768px) {
  .main__container {
    padding-left: 24px;
    padding-right: 24px;
  }
}
@media (min-width: 1440px) {
  .main__container {
    padding-left: 32px;
    padding-right: 32px;
  }
}

.section {
  padding-top: 40px;
  padding-bottom: 40px;
}
@media (min-width: 992px) {
  .section {
    padding-top: 80px;
    padding-bottom: 80px;
  }
}

.section__container {
  max-width: 1440px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 16px;
  padding-right: 16px;
}
@media (min-width: 768px) {
  .section__container {
    padding-left: 24px;
    padding-right: 24px;
  }
}
@media (min-width: 1440px) {
  .section__container {
    padding-left: 32px;
    padding-right: 32px;
  }
}

.section__header {
  max-width: 570px;
  margin: 0 auto;
  text-align: center;
}

.section__small-text {
  font-size: 2rem;
  line-height: 1.4;
  letter-spacing: 0.1;
  font-weight: 700;
  margin-top: 0;
  margin-bottom: 10px;
}

.section__title {
  font-size: 4.8rem;
  margin-top: 0;
  margin-bottom: 20px;
}
.section__title:last-child {
