  margin-bottom: 0px;
}

.features__item-text {
  color: var(--color-secondary);
  font-size: 1.8rem;
  line-height: 1.4;
  margin-top: 0;
}
.features__item-text:last-child {
  margin-bottom: 0;
}

.organize__container {
