  line-height: 1.25;
  letter-spacing: 0.1px;
  margin-top: 0;
  margin-bottom: 10px;
}

.subscribe__text {
  color: var(--color-secondary);
  font-size: 1.8rem;
  line-height: 1.25;
  margin-top: 0;
}
.subscribe__text:last-of-type {
  margin-bottom: 0;
}

.subscribe__form {
  margin-top: 30px;
}

.partners__swiper {
  margin: 80px 0;
  padding: 0;
}

.partners__swiper-wrapper {
