  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 15px;
}

.comment-card__img {
  margin-bottom: 40px;
}

.comment-card__text {
  font-size: 1.8rem;
  line-height: 1.4;
  text-align: center;
  margin-top: 0;
}
.comment-card__text:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .comment-card__text {
    font-size: 2.8rem;
  }
}

.comment-card__author {
  display: flex;
  align-items: center;
