}
@media (min-width: 768px) {
  .comment-card__avatar {
    margin-right: 15px;
  }
}

.comment-card__content {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.comment-card__name {
  font-size: 1.2rem;
  font-weight: 700;
}
@media (min-width: 768px) {
  .comment-card__name {
    font-size: 1.8rem;
  }
}

.comment-card__profession {
  color: var(--color-secondary);
  font-size: 1.5rem;
}
@media (min-width: 768px) {
  .comment-card__profession {
    font-size: 1.8rem;
  }
}

.pricing {
  color: var(--color-white);
  background-color: var(--color-primary);
  padding-top: 80px;
  padding-bottom: 80px;
}

.pricing__text {
  color: var(--color-white);
}

.pricing__list {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  gap: 50px;
  list-style: none;
  margin: 80px 0 0;
